<template>
    <v-dialog max-width="600px">
        <v-btn flat slot="activator" class="success">Add new Project</v-btn>
           <v-card>
               <v-card-title>
                   <h2>Add a new Project</h2>
               </v-card-title>
               <v-card-text>
                   <v-form class="px-3" ref="form">
                    <v-text-field label="Title" v-model="title" prepend-icon="folder" :rules="inputRules"></v-text-field>
                    <v-textarea :rules="inputRules" label="information" v-model="content" prepend-icon="edit"></v-textarea>
                    
                    <v-menu>
                        <v-text-field  :value="formattedDate" slot="activator" label="Due" prepend-icon="date_range" ></v-text-field>
                        <v-date-picker :rules="inputRules" landscape locale="pt-pt" v-model="due" ></v-date-picker>
                    </v-menu>

                    <v-spacer></v-spacer>
                    
                    <v-btn flat class="success mx-0 mt-3" @click="submit">Add Project</v-btn>  
                   </v-form>
               </v-card-text>
           </v-card>
        

    </v-dialog>
</template>

<script>
import format from 'date-fns/format'

export default {
    data() {
        return{
            title: '',
            content:'',
            due:null,
            inputRules: [
                v => v.length >= 3 || 'Inserir pelo menos 3 caracteres'
            ]
        }
    },

    methods: {
        submit(){
            if(this.$refs.form.validate()) {
                console.log(this.title, this.content, this.due)
            }
            
        }
    },

    computed: {
        formattedDate() {
            return this.due ? format(this.due, 'DD MMM YY') : ''
        }

    }
    

}
</script>
